import React from "react";
import { MDBCard, MDBInput } from "mdbreact";

export default function CamaroteItem(props) {
  const imgurl = props.dados.images[0] ? props.dados.images[0].src : "";
  const iditem = "" + props.dados.id;
  return (
    <MDBCard
      className="card-image"
      style={{
        backgroundImage: `url('${imgurl}')`
      }}
    >
      <div className="text-white text-left d-flex align-items-bottom py-5 px-4">
        <MDBInput label="c" type="checkbox" id={iditem} />
      </div>
    </MDBCard>
  );
}
